@import url(http://fonts.googleapis.com/css?family=PT+Sans&subset=latin,cyrillic);
@import 'wp-caption';

body {
    font-family: 'PT Sans', sans-serif;
}

/* ============================================================ */
/* WP core css */
.alignnone {
    margin: 5px 20px 20px 0;
}

.aligncenter,
div.aligncenter {
    display: block;
    margin: 5px auto 5px auto;
}

.alignright {
    float:right;
    margin: 5px 0 20px 20px;
}

.alignleft {
    float: left;
    margin: 5px 20px 20px 0;
}

a img.alignright {
    float: right;
    margin: 5px 0 20px 20px;
}

a img.alignnone {
    margin: 5px 20px 20px 0;
}

a img.alignleft {
    float: left;
    margin: 5px 20px 20px 0;
}

a img.aligncenter {
    display: block;
    margin-left: auto;
    margin-right: auto
}


/* /WP core css */

/* ============================================================ */
/* bootstrap styles */
body {
  position: relative;
}

a {
	outline: none !important;
}

.glyphicon {
	margin-right: 10px;
}

.btn-danger {
	background-color: #ed1a2d;
	border-color: #ed1a2d;
    border-radius: 25px;
    padding: 5px 35px;
}

.btn-primary {
    background-color: #3d3f9d;
    border-color: #3d3f9d;
}

a:hover.btn-primary, a:focus.btn-primary {
    background-color: #583d90;
    border-color: #583d90;
}

ul.wicked-list {
	list-style-image: url(img/li.png);
    padding-left: 18px;
}

ul.wicked-list li {
	padding: 5px;
}

.wicked-bold {
	font-weight: bold;
	font-size: 18px;
}

/* columns of same height styles */
.row-same-height {
    display: table;
}
.col-xs-height {
    display: table-cell;
    float: none !important;
}

@media (min-width: 992px) {
    .col-md-height {
        display: table-cell;
        float: none !important;
    }
}

/* firefox fix */
.img-responsive {
    width: 100%;
}

/* vertical alignment styles */
.col-top {
    vertical-align: top;
}

.col-middle {
    vertical-align: middle;
}

/* silly css */
@media (min-width: 992px) and (max-width: 1550px) {
    .strange-padding-left {
        padding-left: 15% !important;
    }
    .strange-padding-right {
        padding-right: 15% !important;
    }
}
/* /silly css */

